h1 {
  color: white;
}

.page-route {
  .breadcrumb {
    background-color: transparent;
    padding: 0px;

    li {
      padding: 5px;
      color: white;

      svg {
        font-size: 12px;
      }

      &:last-child svg {
        display: none;
      }
    }
  }
}

.items-row {
  background-color: rgb(21, 27, 36);
  padding-top: 15px;
  padding-bottom: 15px;
  .col-md-3{
    border-right: 1px solid rgb(46, 57, 65);
    &:first-child{
      border-right: unset;
    }
  }
  .filterType {
    text-transform: uppercase;
    color: rgb(124, 134, 143);
    font-size: 20px;
    margin: 0;
    margin-bottom: 10px;
  }

  .action-btn{
    position: relative;
    line-height:0;
    user-select: none;
    vertical-align: top;
    height: 32px;
    color: white;
    display: inline-block;
    width: auto;
    box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px;
    background-color: rgb(13, 110, 241);
    transition: background-color 0.1s ease 0s, color 0.4s ease 0s, padding 0.4s ease 0s;
    border-width: 0px;
    border-style: initial;
    border-color: initial;
    border-image: initial;
    border-radius: 2px;
    overflow: hidden;
    padding: 10px 12px;
    text-decoration: none;
  }
  .reset{
    position: relative;
    line-height:0;
    user-select: none;
    vertical-align: top;
    height: 32px;
    color: white;
    display: inline-block;
    width: auto;
    box-shadow: rgba(0, 0, 0, 0) 0px 0px 0px 0px;
    background-color: transparent;
    transition: background-color 0.1s ease 0s, color 0.4s ease 0s, padding 0.4s ease 0s;
    border-width: 1px;
    border-style: solid;
    border-color: white;
    border-image: initial;
    border-radius: 2px;
    overflow: hidden;
    padding: 10px 12px;
    text-decoration: none;
  }

  .more-info-label{
    display: inline-block;
    color: rgb(226, 226, 226);
    background-color: rgb(46, 57, 65);
    margin-right: 0.4rem;
    margin-bottom: 0.8rem;
    cursor: pointer;
    max-width: 100%;
    white-space: normal;
    border-radius: 0.8rem;
    padding: 0.4rem 0.8rem;
    transition: all 0.2s ease-out 0s;
    margin-top: 1.6rem;
    margin-bottom: 1.6rem;
  }
}

.result{
  label{
    font-size: 16px;
    color: rgb(124, 134, 143);
  }

  .result-num{
    color: white;
    font-size: 14px;
  }
}

.w-100{
  width: 100%;
}
