.search {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: left;
  position: relative;
  width: 100%;
  background: rgb(21, 27, 36);
  padding: 30px;

  .search-container {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    z-index: 0;
    overflow: hidden;

    .overlay {
      position: absolute;
      left: 0px;
      top: 0px;
      width: 101%;
      height: 100%;
      mix-blend-mode: normal;
      z-index: 1;
      background: linear-gradient(rgba(40, 50, 62, 0.29) 0%, rgba(40, 52, 62, 0.55) 49.17%, rgba(40, 50, 62, 0.85) 100%);
    }

    .back-img {
      object-fit: cover;
      height: 100%;
      width: 100%;
      filter: blur(0rem);
      opacity: 1;
      transition: all 0.4s ease-out 0s;
      max-width: 100%;
      border-width: 0px;
      border-style: initial;
      border-color: initial;
      border-image: initial;
    }
  }

  .filter-content {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: left;
    flex: 1 1 auto !important;
    color: white;
    z-index: 2;

    .breadcrumb {
      background-color: transparent;

      li {
        padding: 5px;
        color: white;

        svg {
          font-size: 12px;
        }

        &:last-child svg {
          display: none;
        }
      }
    }

  }
}

.MuiInputLabel-formControl {
  right: 0;
  left: unset;
}

.MuiSelect-icon {
  left: 0 !important;
  right: unset !important;
  color: rgba(255, 255, 255, 0.54) !important;
}

.MuiFormLabel-root {
  color: rgba(255, 255, 255, 0.54) !important;
}

.MuiInput-underline:before {
  border-bottom: 1px solid rgba(255, 255, 255, 0.54) !important;;
}

.filter {
  a {
    color: white;
  }
}

.col-md-12 .row .col-md-3:last-child{
  .each-stock{
    margin-left: 0px;
  }
}
.each-stock {
  z-index: 0;
  height: 304px;
  position: relative;
  overflow: unset;
  margin-left: -7.5px;
  margin-right: -7.5px;
  &:before {
    content: " ";
    position: absolute;
    width: 80%;
    height: 92%;
    top: 24px;
    left: 10%;
    box-shadow: rgba(0, 0, 0, 0.4) 0px 10px 24px;
    opacity: 0;
    background: rgb(196, 196, 196);
    transition: all 0.4s ease-out 0s;
  }

  .top-box {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    background-color: rgb(75, 89, 102);
    overflow: hidden;

    .top-box-overlay {
      position: absolute;
      left: 0px;
      top: 0px;
      width: 101%;
      height: 101%;
      mix-blend-mode: normal;
      z-index: 1;
      background: linear-gradient(rgba(40, 50, 62, 0.29) 0%, rgba(40, 52, 62, 0.55) 49.17%, rgba(40, 50, 62, 0.85) 100%);
    }

    .box-img {
      object-fit: cover;
      position: absolute;
      left: 0px;
      top: 0px;
      height: 100%;
      width: 100%;
      transition: all 0.4s ease-in-out 0s;
    }
  }

  .chart-container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: left;

    .chart {
      position: relative;
      z-index: 2;
      max-width: 180px;
      padding-top: 1.6rem;
      margin: auto;
    }
  }


  .more-info{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: left;

    .stock-name{
      -webkit-box-flex: 1;
      width: 100%;
      min-height: 0px;
      max-width: 100%;
      position: relative;
      height: 13.4rem;
      -webkit-box-pack: center !important;
      justify-content: center !important;
      align-content: center !important;
      -webkit-box-align: center !important;
      align-items: center !important;
      flex: 0 0 100%;
      overflow: hidden;
      .name-container{
        position: relative;
        z-index: 2;
        padding-left: 0.8rem;
        padding-right: 0.8rem;
        transition: all 0.3s ease-in-out 0s;

        h3{
          font-size: 16px;
          font-weight: normal;
          line-height: 1.7;
          text-align: center;
          width: 100%;
          white-space: nowrap;
          text-overflow: ellipsis;
          margin: 0px 0px 0px 0px;
          overflow: hidden;
          color: white;
        }

        .type{
          color: rgb(124, 134, 143);
        }
      }
    }
  }
}
